@import url(https://fonts.googleapis.com/css2?family=Kantumruy+Pro:wght@300;400;500;600;700&display=swap);
.app h1 {
  margin: 1rem 0;
  font-size: 70px;
  color: #ffe400;
  text-align: center;
  text-decoration: underline;
}
.card {
    cursor: pointer;
    background: rgb(243, 239, 243);
    border-radius: 20px;
    margin: 2rem 4rem;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 1rem 2rem;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    
}

.card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.card_image {
    width: 200px;
    height: 200px;
}

.card_image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card_info {
    margin-top: 1rem;
}

.card_info h2 {
    margin-bottom: 10px;
    color: black;
}

.card_info h3 {
    color: #272727;
}

.user_list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}

.pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 1rem;
}

.pagination button {
    width: 40px;
    height: 40px;
    font-family: inherit;
    font-weight: 600;
    font-size: 16px;
    margin: 0 10px;
    border-radius: 6px;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    background: transparent;
    color: #eee;
    border-color: #eee;
}

.pagination button.active {
    font-weight: 900;
    border-color: #101010;
    background: hsl(20, 100%, 50%);
    color: #101010;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Kantumruy Pro", sans-serif;
    background-color: #101010;
    color: hsl(0, 0%, 3%);
}

